<template>
    <div class="register-page input-border-none self-mt-header" :style="clientHeight">
		 <header class="post-head">
            <a href="javascript:;" class="back" @click="backHistory"></a> 
            <span class="head-font">换绑手机</span>
        </header>
		<!-- <mt-header title="换绑手机" fixed>
            <router-link to="/qdsPersonal" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
        </mt-header> -->
		<div class="title-logo"></div>
        <div class="register-box">
            <el-form :model="registerForm" status-icon :rules="ruleRegister" ref="ruleRegister"  class="demo-ruleForm">
				<div class="items-line">
					<img :src="loginPhone" alt="">
					<el-form-item label="" prop="mobile">
						<el-input type="text" v-model.trim="registerForm.mobile" auto-complete="off" placeholder="请输入手机号码" maxlength="11"></el-input>
					</el-form-item>
				</div>
				<div class="items-line">
					<img :src="registerCode" alt="">
					<el-input placeholder="请输入验证码" v-model.trim="registerForm.smsCode">
						<template slot="append" > 
							<span v-show="show" @click="getValidateCode">获取验证码</span> 
							<span v-show="!show">重新获取 {{count}} S</span>
						</template>
					</el-input>
				</div>
            </el-form>              
    	</div>
		<div class="zhanwei"></div>
		<div class="login-btn-box">
			<span @click="showChangePhoneSure">确认</span>
		</div>
        
             
            
    </div>

</template>

<script>
import { MessageBox } from 'mint-ui';
import { Notify} from 'vant';
import Util from '@/js/mUtils';
import loginTitle from "@/assets/qds-img/login-title.png";
import loginPhone from "@/assets/qds-img/login-phone.png";
import loginLock from "@/assets/qds-img/login-lock.png";
import loginCode from "@/assets/qds-img/login-code.png";
import registerCode from "@/assets/qds-img/register_code.png";
export default {
    data() {
        return {
            globalApiUrl: domain.testUrl,       // 字典数据
            ossGlobalApiUrl: domain.ossDomain,  // oss全局域名
            qdsGlobalApiUrl: domain.qdsDomain,       // 快招渠道商全局域名
            loginTitle: loginTitle,
			loginPhone: loginPhone,
			loginLock: loginLock,
			loginCode: loginCode,
			registerCode: registerCode,
			contentText:'',
			textShow: false,
            show: true,
            count: '',
            timer: null, 
            registerForm: {
                corpName: "",
                mobile: "",
                smsCode: "",
                passWord: "",
                checkPass: ""
            },
            ruleRegister: {
               
            },
            corpNameTipStyle: false,
            whetherRegister: 'exist',
            corpNameTipFont: '',
            loading: false,
            companyNameList: [],    // 搜索到的公司集合
            list: [{value: 0,label: '正在加载中'}], 
			checked: false,    // 是否同意用户协议
			clientHeight: {
				height: ""
			},
        };
    },
    created() {
		window.addEventListener('resize', this.getHeight);
		this.getHeight()
	},
	destroyed(){
        window.removeEventListener('resize', this.getHeight)
    },
    mounted() {},
    components: {
        // lHeader,
        // lFooter
    },
    methods: {
		 backHistory() {
            window.history.back(-1)
        },
		getHeight(){
            this.clientHeight.height = window.innerHeight - 0 + 'px';
        },
        getValidateCode() {   //获取验证码
            var that = this
            if(that.registerForm.mobile == '') {
				Notify({
					message: '请输入手机号',
					duration: 3000,
					background: '#E6A23C'
				});
            }else if(!that.registerForm.mobile.match(/^1[0-9]{10}$/)) {
				Notify({
					message: '手机号格式不正确',
					duration: 3000,
					type:'warning',
					background: '#E6A23C'
				});
            }else {
				that.axios(that.qdsGlobalApiUrl + 'notValidate/getRegisterSignCode', {
				 	method: 'post',
                    params: {
                        mobile: that.registerForm.mobile,
                    }
                })
                .then(function (response) {
                    if(response.data.success == true){
                        const TIME_COUNT = 60;
                        if (!that.timer) {
                            that.count = TIME_COUNT;
                            that.show = false;
                            that.timer = setInterval(() => {
								if (that.count > 0 && that.count <= TIME_COUNT) {
									that.count--;
								} else {
									that.show = true;
									clearInterval(that.timer);
									that.timer = null;
								}
                            }, 1000)
						}
						Notify({
							message: '发送成功',
							duration: 1500,
							background: '#20D3B3'
						});
                    }else {
						that.timer = null;
						Notify({
							message: response.data.message,
							duration: 3000,
							background: '#FF6262'
						});
                    }                    
                })
                .catch(function (error) {
					 that.timer = null;
                });
            }
		},
			
		showChangePhoneSure() {                                                     //点击确认修改-显示真正修改绑定账号的弹窗
            var token = Util.getCookie('qdsMToken');
            var that = this
            if(that.registerForm.mobile == '') {
				Notify({
					message: '请输入手机号',
					duration: 3000,
					background: '#E6A23C'
				});
            }else if(!that.registerForm.mobile.match(/^1[0-9]{10}$/)) {
				Notify({
					message: '手机号格式不正确',
					duration: 3000,
					background: '#E6A23C'
				});
            }else if(that.registerForm.smsCode == '') {
					Notify({
						message: '请输入验证码',
						duration: 3000,
						background: '#E6A23C'
					});
					return false
			}else {
                that.loading = true
                that.axios(that.qdsGlobalApiUrl + 'changeMobiles?token=' + token, {
                    method: 'post',
                    params: {
                        newMobile: that.registerForm.mobile,
                        smsCode: that.registerForm.smsCode,
                        flage: 'stepOne',
                    },
                    timeout: 10000
                })
                .then(function (response) {
                    if(response.data.code == '200') {
						that.loading = false
                       	MessageBox({
							title: '提示',
							message: '修改成功后, 您登陆的手机号将发生改变，是否继续修改？',
							showCancelButton: true
						}).then(action => {
							if(action == 'confirm'){
								that.changePhoneSure()
							}
							
						}).catch(error => {
						})
                    } else if(response.data.code == '411') {
						that.loading = false
						Notify({
							message: '验证码输入错误，请重新输入',
							duration: 3000,
							background: '#E6A23C'
						}); 
                    }else if(response.data.code == '412') {
						that.loading = false
						Notify({
							message: '该手机号已经绑定用户名，不能再做修改',
							duration: 3000,
							background: '#E6A23C'
						}); 
                    }else {
						that.loading = false 
						Notify({
							message: response.data.message,
							duration: 3000,
							background: '#E6A23C'
						}); 
                    }
                })
                .catch(function (error) {
                    that.loading = false 
                });
            }
		},
		changePhoneSure() {                                                          //确认修改手机号
            var token = Util.getCookie('qdsMToken');
            var that = this
            that.loading = true 
            that.axios(that.qdsGlobalApiUrl + 'changeMobiles?token=' + token, {
                method: 'post',
                params: {
                   	newMobile: that.registerForm.mobile,
                    smsCode: that.registerForm.smsCode,
                    flage: 'stepTwo',
                },
                timeout: 10000
            })
            .then(function (response) {
                if(response.data.code == '200') {
					that.loading = false 
					Notify({
						message: '成功修改绑定手机号',
						duration: 3000,
						background: '#20D3B3'
					}); 
                    setTimeout(function(){
                        if(location.href.indexOf("m.qh.uat.youlanw.com") == -1) {
                            document.cookie = "qdsToken" + "=" + '' + ";path=/"
                            sessionStorage.removeItem("qdsCertificationStatus");
                            localStorage.removeItem("qdsSCorpUserId");  
                            that.$router.push({path: '/'});
                        }else {
                            document.cookie = "qdsToken" + "=" + '' + ";domain=m.qh.uat.youlanw.com;path=/";
                            sessionStorage.removeItem("qdsCertificationStatus");
                            localStorage.removeItem("qdsSCorpUserId"); 
                            that.$router.push({path: '/'}); 
                        }
                    },1100)
                }else if(response.data.code == '412') {
					that.loading = false 
					Notify({
						message: response.data.message,
						duration: 3000,
						background: '#FF6262'
					});
                }else {
                    that.loading = false 
                    Notify({
						message: response.data.message,
						duration: 3000,
						background: '#FF6262'
					});
                }
            })
            .catch(function (error) {
				that.loading = false 
				Notify({
					message: '修改手机号操作异常，请联系客服021-22876396',
					duration: 2000,
					type:'error',
					background: '#FF6262'
				});
            });
        },
    },
        
      
}

</script>

<style scoped>
	.post-head{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        display: -webkit-flex;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 2.6875rem;
        border-bottom: 1px solid #F5F5F5;
        background-color: #fff;
    }
    .back{
        position: absolute;
        left: .9375rem;
        display: inline-block;
        width: .5625rem;
        height: 1.1875rem;
        background: url(../../assets/post/back_icon.png);
        background-size: 100% 100%;
        cursor: pointer;
    }
    .head-font{
        font-size: 1rem;
        color: #0B231F;
        font-weight: bold;
    }  
	input {
		margin: 0;
		padding: 0;
		border: none;
	}
	img{
		vertical-align: middle;
		border: none;
	}
	.register-page {
		height: 100%;
		width: 100%;
		background: #fff;
	}
	.register-page .title-logo{
		height: 3.75rem;
	}
	.register-page .register-box {
		padding-top: .5rem;
		margin: 0 2rem;
	}
	.register-page .register-box .items-line {
		display: -webkit-flex;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: flex-start;
		height: 4rem;
		line-height: 4rem;
		border-bottom: 1px solid #E5E5E5;
	}
	.register-page .register-box .items-line img {
		width: .875rem;	
		padding-left: 20px;
	}

	.register-page .select-agreement{
		font-size: .75rem;
		padding: 1.4375rem 0 .6875rem 2.8125rem;
	}
	.register-page .select-agreement p input{
		display: none;
	}
	.register-page .select-agreement p .agreement_btn+label{
		display: inline-block;
		width: .8125rem;
		height: .8125rem;
		border: 1px solid gray;
		border-radius: 50%;
		
	}


	.register-page .login-btn-box {
		width: 80%;
		margin: 0 auto;
		text-align: center;
	}
	.register-page .login-btn-box span{
		display: inline-block;
		width: 100%;
		border-radius: 1.375rem;
		background: #2da1a4;
		line-height: 2.75rem;
		color: #fff;
		font-size: 1.0625rem;
	}
	.register-page .select-agreement .user-agreement{
		color: #2da1a4;
	}
	.zhanwei {
		height: 1.875rem;
	}
	.register-page .footer{
		letter-spacing: .1em;
		width:60% ;
		margin: 0 auto;
		display: -webkit-flex;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		font-size: .8125rem;
		color: #818D8D;
		font-size: .8125rem;
	}
	.register-page .footer .go-login-btn{
		color: #2da1a4;
	}
	.open-text {
		width: 80%;
	}

</style>



